import Vue from 'vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'



Vue.use(ToastPlugin)
Vue.use(AjaxPlugin)

const $http = Vue.http
export const domain = 'http://192.168.225.106:7001'


$http.defaults.timeout = 1000 * 60 * 5

/** ajax获取信息
* @params url | 请求地址
* @params data | 请求参数
* @params successCb | 成功
* @params errorCb | 失败
* @params method |
*
* @return boolean
*/
export const fetch = function (url, data, successCb, errorCb, method) {
method = method || 'POST'
data = data || {}
$http({method, url, data}).then(response => {




successCb && typeof successCb === 'function' && successCb(response)
}).catch(error => {
errorCb && typeof errorCb === 'function' && errorCb(error)
})
}

/** 设置/更新用户信息
* @params key | 设置字段 String
* @params value | 设置值 String
*
* @return void(0)
*/
export const setUserInfo = function (key, value) {
let userInfo = localStorage.getItem('userInfo')
if (userInfo == null || userInfo === '') {
userInfo = '{}'
}
userInfo = JSON.parse(userInfo)
userInfo[key] = value
localStorage.setItem('userInfo', JSON.stringify(userInfo))
}

/** 删除用户信息
*
* @return void(0)
*/
export const deleteUserInfo = function () {
localStorage.setItem('userInfo', '')
}

/** 获取指定用户信息
* @params key | 字段
*
* @return Json
*/
export const getSingleUserInfo = function (key) {
let userInfo = localStorage.getItem('userInfo')
if (userInfo == null || userInfo === '') {
userInfo = '{}'
}
userInfo = JSON.parse(userInfo)
return userInfo[key]
}

/** 认证跳转
* @params vm | vue对象 object
* @params jumpUrl | string
*
* @return type
*/
export const isAuthJumpTo = function (app, jumpUrl) {
let authFlag = getSingleUserInfo('user_auth')
if (authFlag === 1) {
app.$vux.alert.show({
title: '重要提醒',
content: '必须先<strong>实名认证（1/2）</strong>，才能提取收益至您的银行卡。请立即认证',
onHide: () => {
app.$router.push('/certification')
}
})
return false
}
if (authFlag === 2) {
app.$vux.alert.show({
title: '重要提醒',
content: '必须先<strong>实名认证（2/2）</strong>，才能提取收益至您的银行卡。请立即认证',
onHide: () => {
app.$router.push('/authChangeIdCard')
}
})
return false
}
app.$router.push(jumpUrl)
}



