
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import dateFormat from 'vux/src/tools/date/format.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import PullTo from 'vue-pull-to'
import * as base from '@/api/base'
import { checkLoginToLogin } from '@/api/login'

Vue.use(ToastPlugin)
Vue.use(AjaxPlugin)
Vue.use(ConfirmPlugin)

export default {
name: 'index',
components: {
XHeader,
Flexbox,
FlexboxItem,
XButton,
PullTo,
Badge
},
data () {
return {
dataList: [],
iconLink: '',
me: {},
childCount: 0,
children: [],
lastLoginDate: '',
sortId: '',
searchShortId: '',
searchData: null,
page: 1,
isNotLoaded: true,
isAgent: base.getSingleUserInfo('user_identity').current === 'agent',
pdtop: '46px',
tipsAys: '正在检索用户'
}
},
methods: {
loadmore (loaded) {
this.getCustromerList(true, loaded)
},
stateChange (state) {
if (state === 'pull' || state === 'trigger') {
this.iconLink = '#icon-arrow-bottom'
} else if (state === 'loading') {
this.iconLink = '#icon-loading'
} else if (state === 'loaded-done') {
this.iconLink = '#icon-finish'
}
},
turnCard (item) {
let shortId = item ? item.shortId : this.sortId
let nickname = item ? (item.nickname ? item.nickname : '未定义') : '自己'
this.$router.push({
path: '/turn_the_card/' + shortId + '?nickname=' + nickname + '&stock=' + this.me.coin.stock
})
},
getCustromerList (isLoad = false, loaded) {
let strUrl = '/api/v1/agentcards?limit=5&page=' + this.page
base.fetch(strUrl, null, response => {
let data = response.data
let msg = data.msg || data.error || '请求失败'
let identity = base.getSingleUserInfo('user_identity')
if (data.code !== 0) {
this.$vux.toast.text(msg, 'middle')
return false
}
this.me = data.data[identity.current + 'Info']
this.childCount = data.data.children.childCount
this.children = this.children.concat(data.data.children.children)
this.lastLoginDate = dateFormat(new Date(data.data.children.date), 'YYYY-MM-DD HH:mm:ss')
this.sortId = data.data.children.shortId
this.page ++
if (isLoad) loaded('done')
if (this.childCount <= this.children.length) {
if (isLoad) {
this.$vux.toast.text('列表已全部加载完成')
}
this.isNotLoaded = false
}
}, error => {
console.log(error)
}, 'GET')
},
deleteCustomer (item) {
let me = this
this.$vux.confirm.show({
title: '确认解绑该用户？',
onConfirm () {
base.fetch('/api/v1/agents/untyingUsers', {
'childrenShortId': item.shortId.toString()
}, response => {
let data = response.data
let msg = data.msg || data.error || '请求失败'
me.$vux.toast.text(msg, 'middle')
if (data.code !== 0) {
return false
}
location.reload(false)
}, error => {
console.log(error)
}, 'POST')
}
})
},
searchCustmer () {
if (this.searchShortId === '') {
this.$vux.toast.text('请输入用户ID', 'middle')
return false
}
base.fetch('/api/v1/agentcards/' + this.searchShortId, null, response => {
let data = response.data
if (data.code !== 0) {
this.$vux.toast.text(data.msg, 'middle')
this.tipsAys = data.msg
return false
}
this.searchData = data.data
}, error => {
this.$vux.toast.text('请求不合法', 'middle')
console.log(error)
}, 'GET')
},
resetSearch () {
this.searchData = null
this.searchShortId = ''
}
},
created () {
if (this.isAgent) {
this.pdtop = '0px'
}
this.getCustromerList()
checkLoginToLogin(this)
},
watch: {
searchShortId () {
this.tipsAys = '正在检索用户'
}
}
}



