
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { checkLoginToLogin } from '@/api/login'
import * as base from '@/api/base'
import '@/assets/tabbar/iconfont.css'
export default {
name: 'incontent',
directives: {
TransferDom
},
components: {
Tabbar,
TabbarItem
},
data () {
return {
tabLinkArr: ['/index/account', '/index/customer', '/index/invitation', '/index/mine'],
tabbarData: [
{
route: '/index/account',
name: '收益',
icon: 'iconfont icon-leijishouyi',
selected: true
},
{
route: '/index/customer',
name: '用户',
icon: 'iconfont icon-1',
selected: false
},
{
route: '/index/invitation',
name: '邀请',
icon: 'iconfont icon-yaoqingtongshi',
selected: false
},
{
route: '/index/mine',
name: '我的',
icon: 'iconfont icon-user',
selected: false
}
],
tabIndex: 0
}
},
methods: {
indexChange (index) {
sessionStorage.setItem('changeTabbar', index)
}
},
computed: {
tabbarIndex: {
get () {
return parseInt(this.tabIndex)
},
set () {}
}
},
created () {
if (base.getSingleUserInfo('user_identity') && base.getSingleUserInfo('user_identity').current === 'agent') {
this.tabbarData[1].name = '下挂馆长'
this.tabbarData[1].route = '/index/the_hang_curator'
}
this.tabIndex = sessionStorage.getItem('changeTabbar')
checkLoginToLogin(this)
},
beforeRouteUpdate (to, from, next) {
for (var i = 0; i < this.tabLinkArr.length; i++) {
if (to.path === this.tabLinkArr[i]) {
sessionStorage.setItem('changeTabbar', i)
this.tabIndex = i
break
}
if (to.path === '/index/the_hang_curator') {
sessionStorage.setItem('changeTabbar', 1)
this.tabIndex = 1
}
}
next()
}
}



