
import Masker from 'vux/src/components/masker/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Scroller from 'vux/src/components/scroller/index.vue'

import Notices from './components/notices'
import Actions from './components/actions'
import Statistical from './components/statistical'
import PullTo from 'vue-pull-to'
import { checkLoginToLogin } from '@/api/login'
import * as base from '@/api/base'
import { agentQuery } from '@/api/keen'
import _ from 'lodash'
export default {
name: 'index',
components: {
Notices, Actions, Statistical, Masker, Flexbox, FlexboxItem, Divider, XTable, XHeader, Popup, Group, Cell, Badge, Scroller, PullTo
},
directives: {
TransferDom
},
data () {
return {
identity: base.getSingleUserInfo('user_identity') && base.getSingleUserInfo('user_identity').current,
identityState: false,
identityName: '',
identityData: base.getSingleUserInfo('user_identity'),
otherIndentity: '代理',
otherEnIndentity: 'agent',
statisticalServer: {},
title: '棋牌室管理系统',
agentTodayIncome: 0, // 代理今日收入
todayInStock: 0, // 代理今日进货额
monthAgentIncome: 0,
agentSaleCount: 0,
chartsList: [],
todayAddUserCount: 0,
todayDate: new Date()
}
},
created () {
checkLoginToLogin(this)
if (this.identityData) {
this.identityName = this.identityData.current === 'agent' ? '代理' : '馆长'
this.otherIndentity = this.identityData.current === 'agent' ? '馆长' : '代理'
this.otherEnIndentity = this.identityData.current === 'agent' ? 'curator' : 'agent'
this.title = this.identityData.current === 'agent' ? '代理管理系统' : '棋牌室管理系统'
if (this.identityData.current === 'agent') {
base.fetch('/api/v1/agents/agentdatacount', null, response => {
let data = response.data
this.statisticalServer = data.data
}, error => {
console.log(error)
}, 'GET')
}
}
if (base.getSingleUserInfo('user_agentId')) {

agentQuery.agentCurrentIncome(base.getSingleUserInfo('user_agentId'), 'this_1_months', data => {
this.monthAgentIncome = data.result
})

agentQuery.agentCurrentIncome(base.getSingleUserInfo('user_agentId'), 'this_1_days', data => {
this.agentTodayIncome = data.result
})

agentQuery.customerInStock(base.getSingleUserInfo('user_agentId'), 'agentParent', 1, data => {
this.todayInStock = data.result
})
base.fetch('/api/v1/agents/getshortid', null, response => {
let data = response.data
if (data.code !== 0) return false
let allCustomerList = _.map(data.data, 'shortId')
agentQuery.saleVolume(allCustomerList, data => {
this.agentSaleCount = data.result
})
}, error => {
console.log(error)
}, 'GET')

base.fetch('/api/v1/agents/getusercharts?type=day&limit=5&page=0', null, response => {
let data = response.data
this.chartsList = data
}, error => {
console.log(error)
}, 'GET')

agentQuery.queryAgentAddUser(base.getSingleUserInfo('user_agentId'), data => {
this.todayAddUserCount = data.result
})
}
},
methods: {
alertNote () {
this.$vux.alert.show({
title: '说明',
content: '<div style="text-align:left;"><p>1.收入=棋牌室代销额*65%</p><p>2.请馆长自行报税，税务问题与369平台无关</p><p>3.实际到账时间，以银行到账时间未准</p><p>4.若遇到任何收入计算问题，请随时咨询客服，电话：400-0348-369，微信公众号：slj-qipai</p></div>'
})
},
toMessage: function () {
this.$router.push('/message')
},
identityMethod: function () {
if (this.identityData.identity === 'agentSuper') {
this.identityState = true
}
},
choiceIdentity () {
base.fetch('/api/v1/agents/switchidentity', {
identity: this.otherEnIndentity
}, response => {
let data = response.data
if (data.code !== 0) {
this.$vux.toast.text('切换失败', 'middle')
return false
}
base.deleteUserInfo()
for (let k in data.data) {
base.setUserInfo('user_' + k, data.data[k])
}
this.$vux.toast.text('切换成功', 'middle')
this.$router.replace('/')
location.reload()
}, error => {
console.log(error)
})
this.identityState = false
}
}
}



