/* eslint-disable */
import Vue from 'vue'
import Router from 'vue-router'
import Index from '../views/Index'
import store from '@/store'
import Account from '../views/Account'
import InContent from '../views/InContent'
import Invitation from '../views/Invitation'
import Mine from '../views/Mine'
import theHangCurator from '../views/agent/theHangCurator'
import commonRoutes from './common'
import Customer from '../views/Customer'
import * as base from '@/api/base'
import { checkLoginToLogin } from '@/api/login'
const history = window.sessionStorage
// history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)
let isPush = false
let endTime = Date.now()
let methods = ['push', 'go', 'replace', 'forward', 'back']
document.addEventListener('touchend', () => {
  endTime = Date.now()
})

Vue.use(Router)

let routeArr = [
  {
    path: '/',
    name: 'Index',
    component: Index,
    redirect: '/index/account',
    children: [
      {
        path: 'index',
        component: InContent,
        children: [
          {
            path: 'account',
            component: Account
          },
          {
            path: 'invitation',
            component: Invitation
          },
          {
            path: 'mine',
            component: Mine
          },
          {
            path: 'customer',
            component: Customer
          },
          {
            path: 'the_hang_curator',
            component: theHangCurator
          }
        ]
      }
    ]
  },
  {
    'path': '/agent_turn_card',
    component: Customer
  }
]
let routes = routeArr.concat(commonRoutes)
let router = new Router({ routes })

methods.forEach(key => {
  let method = router[key].bind(router)
  router[key] = function (...args) {
    isPush = true
    method.apply(null, args)
  }
})

// 配置导航路由
router.beforeEach((to, from, next) => {
  // const toIndex = history.getItem(to.path)
  // const fromIndex = history.getItem(from.path)
  //
  // if (toIndex) {
  //   if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
  //     store.commit('updateDirection', {direction: 'forward'})
  //   } else {
  //     // 判断是否是ios左滑返回
  //     if (!isPush && (Date.now() - endTime) < 377) {
  //       store.commit('updateDirection', {direction: ''})
  //     } else {
  //       store.commit('updateDirection', { direction: 'reverse' })
  //     }
  //   }
  // } else {
  //   historyCount++
  //   history.setItem('count', historyCount)
  //   to.path !== '/' && history.setItem(to.path, historyCount)
  //   store.commit('updateDirection', {direction: 'forward'})
  // }

  if (to.path === '/index/account') {
    base.fetch('/api/v1/agentcards?limit=5&page=1', null, response => {
      let data = response.data
      if (data.code === -2 || data.code === -1) {
        localStorage.clear()
        next('/login')
      }
    }, error => {
      console.log(error)
    }, 'GET')
  }
  if (/\/http/.test(to.path)) {
    let url = to.path.split('http')[1]
    window.location.href = `http${url}`
  } else {
    next()
  }
})

export default router



