
import XHeader from 'vux/src/components/x-header/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import { checkLoginToLogin } from '@/api/login'
import * as base from '@/api/base'
export default {
name: 'index',
components: {
XHeader,
ButtonTab,
ButtonTabItem,
Tab,
TabItem,
Sticky,
Swiper,
SwiperItem,
XButton,
Qrcode,
Scroller
},
methods: {
switchTabItem (index) {
this.index01 = index
},
screenshots () {},
screenshots2 () {}
},
data () {
return {
index01: 0,
isAgent: base.getSingleUserInfo('user_identity').current === 'agent',
joinCurator: '',
joinAgent: ''
}
},
created () {
checkLoginToLogin(this)
base.fetch('/api/v1/agents/getfriendcard', null, response => {
let data = response.data
if (data.code === 0) {
this.joinCurator = data.data
}
}, error => {
console.log(error)
}, 'GET')
if (this.isAgent) {
base.fetch('/api/v1/agents/getcuratorcard', null, response => {
let data = response.data
if (data.code === 0) {
this.joinAgent = data.data
}
}, error => {
console.log(error)
}, 'GET')
}
}
}



