import KeenAnalysis from 'keen-analysis'

const projectId = '5a7169edc9e77c00011f7a15'
const readKey = '3C0B01A46022DB0AFF8F416D88D5C0891402914BC31D3EE3B8831DA5D4B681D5A534F358F3A35706CA49246628D0207EC8E49AB66564C57BAEC42E395A2F72CF2B3067429543D797C2D5E85A65A70A97E61D6F8CD43F1EC3A6FCD432E2E828BE'

/* eslint-disable */
class AgentQuery {
  constructor (projectId, readKey) {
    this.keen = new KeenAnalysis({
      projectId: projectId,
      readKey: readKey
    })
  }

  /** 代理 收入
   * @params _id | String
   * @params timeframe | String 相对范围
   */
  agentCurrentIncome (curatorParent, timeframe, sucessCb) {
    this.keen.query('sum', {
      event_collection: 'onAgentAward',
      filters: [{'operator':"eq","property_name":"agent","property_value": curatorParent}],
      target_property: "money",
      timeframe: timeframe,
      timezone: "Asia/Singapore"
    }).then(function (res) {
      sucessCb && typeof sucessCb === 'function' && sucessCb(res)
    }).catch(function (err) {
      console.log(err.message)
    })
  }

  /** 馆长 收入
   * @params _id | String
   * @params timeframe | String 相对范围
   */
  curatorCurrentIncome (_id, timeframe, sucessCb) {
    this.keen.query('sum', {
      event_collection: 'onCuratorAward',
      filters: [{'operator':"eq","property_name":"curator","property_value": _id}],
      target_property: "money",
      timeframe: timeframe,
      timezone: "Asia/Singapore"
    }).then(function (res) {
      sucessCb && typeof sucessCb === 'function' && sucessCb(res)
    }).catch(function (err) {
      console.log(err.message)
    })
  }

  /** 代理/馆长 进货额
   * @params _id | String
   * @params identity | 身份标识 value:agentParent || curatorParent
   * @params days | Number 查询天数
   *
   * @return type
   */
  customerInStock (_id, identity, days = 1, successCb) {
    this.keen.query("sum", {
      event_collection: "onUserPay",
      filters: [{"operator":"eq","property_name":identity,"property_value": _id}],
      target_property: "money",
      timeframe: "this_" + days + "_days",
      timezone: "Asia/Singapore"
    }).then(function(res) {
      successCb = arguments[arguments.length - 1]
      successCb && typeof successCb === 'function' && successCb(res)
    }).catch(function(err) {
      console.log(err.message);
    });
  }

  /** 包含所有shortID的销售额
   *
   * @return type
   */
  saleVolume (searchArr, successCb) {
    this.keen.query("sum", {
      event_collection: "onUserPay",
      filters: [{"operator":"in","property_name":"shortId","property_value":searchArr}],
      target_property: "money",
      timeframe: "this_1_days",
      timezone: "Asia/Singapore"
    })
      .then(function(res) {
        successCb && typeof successCb === 'function' && successCb(res)
      })
      .catch(function(err) {
        console.log(err.message);
      });
  }

  /** 查询代理某天收入
   * @params name | type
   * @params name | type
   *
   * @return type
   */
  queryAgentOneDayIncome (agentId, strDate, successCb) {
    this.keen.query("sum", {
      event_collection: "onAgentAward",
      filters: [{"operator":"eq","property_name":"agent","property_value":agentId}],
      target_property: "money",
      timeframe: {"end":strDate + "T23:45:00.000",start:strDate + "T00:00:00.000"},
      timezone: "Asia/Singapore"
    })
      .then(function(res) {
        successCb && typeof successCb === 'function' && successCb(res)
      })
      .catch(function(err) {
        console.log(err.message);
      });
  }

  /** 查询馆长某天收入
   * @params name | type
   * @params name | type
   *
   * @return type
   */
  queryCuratorOneDayIncome (curatorId, strDate, successCb) {
    this.keen.query("sum", {
      event_collection: "onCuratorAward",
      filters: [{"operator":"eq","property_name":"agent","property_value":curatorId}],
      target_property: "money",
      timeframe: {"end":strDate + "T23:45:00.000","start":strDate + "T00:00:00.000"},
      timezone: "Asia/Singapore"
    })
      .then(function(res) {
        successCb && typeof successCb === 'function' && successCb(res)
      })
      .catch(function(err) {
        console.log(err.message);
      });
  }

  /** 查询代理当天新增馆内用户
   * @params name | type
   * @params name | type
   *
   * @return type
   */
  queryAgentAddUser (agentid, successCb) {
    this.keen.query("count", {
      event_collection: "onChessRoomAddUser",
      filters: [{"operator":"eq","property_name":"agent","property_value": agentid}],
      timeframe: "this_1_days",
      timezone: "Asia/Singapore"
    })
      .then(function(res) {
        successCb && typeof successCb === 'function' && successCb(res)
      })
      .catch(function(err) {
        console.log(err.message);
      });
  }
}

let agentQuery = new AgentQuery(projectId, readKey)

export { agentQuery }



