
import XHeader from 'vux/src/components/x-header/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import pullTo from 'vue-pull-to'
import * as base from '@/api/base'
export default {
name: 'the-hang-curator',
components: {
XHeader,
Masker,
XButton,
FlexboxItem,
Flexbox,
Checker,
CheckerItem,
DatetimePlugin,
XInput,
Group,
Datetime,
dateFormat,
pullTo
},
data () {
return {
dataList: [],
iconLink: '',
underCuratorCount: 0,
insideCuratorCount: 0,
underCuratorList: [],
page: 1,
count: 0,
isNotLoaded: true,
searchValue: '',
searchCustmer: {},
searchTips: '正在查找'
}
},
methods: {
loadmore (loaded) {
this.fetchList(true, loaded)
},
stateChange (state) {
if (state === 'pull' || state === 'trigger') {
this.iconLink = '#icon-arrow-bottom'
} else if (state === 'loading') {
this.iconLink = '#icon-loading'
} else if (state === 'loaded-done') {
this.iconLink = '#icon-finish'
}
},
fetchList (isLoad, loaded) {
let strUrl = '/api/v1/agentcards?limit=2&page=' + this.page
base.fetch(strUrl, null, response => {
let data = response.data
if (data.code !== 0) return false
data = data.data
this.underCuratorCount = data.children.childCount
this.insideCuratorCount = data.children.userCount

if (isLoad) {
this.dataList = this.dataList.concat(data.children.children)
this.$nextTick(() => {
loaded('done')
})
} else {
this.dataList = data.children.children
}

if (this.dataList.length >= data.children.childCount) {
this.$vux.toast.text('已加载全部数据')
this.isNotLoaded = false
}
}, error => {
console.log(error)
}, 'GET')
},
searchUser () {
base.fetch('/api/v1/agentcards/' + this.searchValue, null, response => {
let data = response.data
if (data.code !== 0) {
this.searchTips = data.msg
return false
}
this.searchCustmer = data.data
console.log(this.searchCustmer)
}, error => {
console.log(error)
}, 'GET')
}
},
created () {
this.fetchList()
},
watch: {
searchValue (val) {
if (val === '') {
this.searchTips = '正在查找'
this.searchCustmer = {}
}
}
}
}



