export default [
  {
    path: '/turn_the_card/:id',
    component: resolve => require(['../views/Customer/TurnTheCard'], resolve)
  },
  {
    path: '/in_stock',
    component: resolve => require(['../views/Customer/InStock'], resolve)
  },
  {
    path: '/reflect',
    component: resolve => require(['../views/Customer/Reflect'], resolve)
  },
  {
    path: '/withdrawal_history',
    component: resolve => require(['../views/Customer/WithdrawalHistory'], resolve)
  },
  {
    path: '/stock_history',
    component: resolve => require(['../views/Customer/stockHistory'], resolve)
  },
  {
    path: '/certification',
    component: resolve => require(['../views/Customer/Certification'], resolve)
  }
]



