
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import * as base from '@/api/base'
export default {
components: {
Cell,
Group
},
data () {
return {
user_id: base.getSingleUserInfo('user_shortId'),
isAuth: '未认证',
isAuthClass: {isAuth: false}
}
},
methods: {
gotoInformation: function () {
this.$router.push({path: '/PersonalInformation'})
},
gotoMyChessRoom: function () {
this.$router.push({path: '/myChessRoom'})
}
},
created () {
switch (base.getSingleUserInfo('user_auth')) {
case 1:
this.isAuth = '未认证'
this.isAuthClass.isAuth = false
break
case 2:
this.isAuth = '未填写银行卡'
this.isAuthClass.isAuth = false
break
case 3:
this.isAuth = '已认证'
this.isAuthClass.isAuth = true
break
default:
break
}
}
}



