
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import * as base from '@/api/base'
export default {
components: {
Flexbox,
FlexboxItem,
XButton
},
methods: {
toTurnCard () {
if (this.isAgent) {
return this.$router.push('/agent_turn_card')
}
this.$router.push('/index/customer')
},
toInStock () {
this.$router.push('/in_stock')
},
jumpToCash () {
base.isAuthJumpTo(this, '/reflect')
}
},
data () {
return {
isAgent: base.getSingleUserInfo('user_identity') && base.getSingleUserInfo('user_identity').current === 'agent'
}
}
}



