


export default {










data () {
return {




msg: 'Hello World!',
tabLinkArr: ['/account', '/customer', '/invitation', '/mine']
}
},
methods: {},
computed: {
direction: {
get: function () {
return this.$store.state.direction
},
set: function () {}
},
viewTransition () {
if (!this.direction) return ''
return 'vux-pop-' + (this.direction === 'forward' ? 'in' : 'out')
}
},
created () {

}
}



