
import Vue from 'vue'
import Masker from 'vux/src/components/masker/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

import Pie from '@/components/Pie'
import * as base from '@/api/base'
import { agentQuery } from '@/api/keen'
import _ from 'lodash'
Vue.use(AlertPlugin)
export default {
name: 'statistical',
components: {
Masker,
Flexbox,
FlexboxItem,
Divider,
XTable,
Pie
},
data () {
return {

childrenCount: base.getSingleUserInfo('user_curatorChildCount'),
statisticalServer: {},
chartsList: [], // 馆内消费排行榜
todayDate: new Date(),
dayCuratorIncome: 0,
curatorSaleCount: 0,
monthCuratorIncome: 0
}
},
methods: {
alertNote () {
this.$vux.alert.show({
title: '说明',
content: '<div style="text-align:left;"><p>1.收入=棋牌室代销额*65%</p><p>2.请馆长自行报税，税务问题与369平台无关</p><p>3.实际到账时间，以银行到账时间未准</p><p>4.若遇到任何收入计算问题，请随时咨询客服，电话：400-0348-369，微信公众号：slj-qipai</p></div>'
})
}
},
created () {

base.fetch('/api/v1/agents/curatordatacount', null, response => {
let data = response.data
if (data.code !== 0) {
console.log(data)
return false
}
this.statisticalServer = data.data
}, error => {
console.log(error)
}, 'GET')

base.fetch('/api/v1/agents/getusercharts?type=day&limit=5&page=0', null, response => {
let data = response.data
this.chartsList = data
}, error => {
console.log(error)
}, 'GET')

agentQuery.curatorCurrentIncome(base.getSingleUserInfo('user_curatorId'), 'this_1_days', data => {
this.dayCuratorIncome = data.result
})

agentQuery.curatorCurrentIncome(base.getSingleUserInfo('user_curatorId'), 'this_1_months', data => {
this.monthCuratorIncome = data.result
})

base.fetch('/api/v1/agents/getshortid', null, response => {
let data = response.data
if (data.code !== 0) return false
let allCustomerList = _.map(data.data, 'shortId')
agentQuery.saleVolume(allCustomerList, data => {
this.curatorSaleCount = data.result
})
}, error => {
console.log(error)
}, 'GET')
}
}



